  parseLoc(node){
    let pnode = JSON.parse(this.globalservice.decodeHtml(node.location));
    let res = "";
    for (let [key, value] of Object.entries(pnode)) {
      // console.log(`${key}: ${value}`);
      res += `${key.toUpperCase()}: ${value}`+" || ";
    }

    // console.log(pnode);
    // console.log(res);
    return res.slice(0, -3);
  }


